import { Injectable } from '@angular/core';
import { Question } from '../models/Question'

@Injectable()
export class DataService {

  questions:Question[];

  constructor() {
  }

  getQuestions(){
    //Init question list if null
    if(localStorage.getItem('MyQuestions') === null){
      this.questions = [];
      localStorage.setItem('MyQuestions', JSON.stringify(this.questions));
    }
    else {
      this.questions = JSON.parse(localStorage.getItem('MyQuestions'));
    }
    return this.questions;
  }

  addQuestion(question:Question){
    this.questions.unshift(question);
    localStorage.setItem('MyQuestions', JSON.stringify(this.questions));
  }

  removeQuestion(question:Question){
    for(let i = 0; i < this.questions.length; i++){
      if(this.questions[i] == question){
        this.questions.splice(i, 1);
        localStorage.setItem('MyQuestions', JSON.stringify(this.questions));
      }
    }
  }

}



